
import { defineComponent, ref } from 'vue'
import { defaultList } from '@/assets/menuList'

export default defineComponent({
  name: 'viewInput',
  setup () {
    const baseInput = ref<string>('')
    const disabledInput = ref<string>('')
    const clearInput = ref<string>('')
    const roundInput = ref<string>('')
    const passwordInput = ref<string>('')
    const searchInput = ref<string>('')
    const searchInput2 = ref<string>('')
    const suffixInput = ref<string>('')
    const prefixInput = ref<string>('')
    const textareaInput = ref<string>('')
    const sizeInput = ref<string>('')

    const base = '<fx-input class="mr10" v-model="baseInput" placeholder="请输入内容"></fx-input>'
    const disabled = '<fx-input class="mr10" v-model="disabledInput" disabled placeholder="请输入内容"></fx-input>'
    const clear = '<fx-input class="mr10" v-model="clearInput" clear placeholder="请输入内容"></fx-input>'
    const round = '<fx-input class="mr10" v-model="roundInput" round placeholder="请输入内容"></fx-input>'
    const password = '<fx-input class="mr10" v-model="passwordInput" password placeholder="请输入内容"></fx-input>'
    const search = `<fx-autocomplete class="mr10" v-model="searchInput" placeholder="请输入密码" clear :fetch="querySearch"></fx-autocomplete>

export default defineComponent({
    setup() {
        const querySearch = (searchText: string, cb: (val: { value: string, role: string }[]) => void) => {
          const restaurants = [
            { value: '蒙奇·D·路飞', role: '船长' },
            { value: '罗罗诺亚·索隆', role: '剑士' },
            { value: '文斯莫克·山治', role: '厨师' },
            { value: '托尼托尼·乔巴', role: '医生' },
            { value: '娜美', role: '航海士' },
            { value: '乌索普', role: '狙击手' },
            { value: '妮可·罗宾', role: '考古学家' },
            { value: '弗兰奇', role: '船匠' },
            { value: '布鲁克', role: '音乐家' },
            { value: '甚平', role: '舵手' }
          ]
          const results = searchText ? restaurants.filter((item: { value: string, role: string }) => {
            return item.value.match(searchText)
          }) : restaurants
          cb(results)
      }
    }
})`
    const search2 = `<fx-autocomplete class="mr10" v-model="searchInput2" placeholder="请输入密码" clear :fetch="querySearch" searchKey="role">
  <template v-slot="{item}">
    <fx-link type="primary">{{ item.role }}</fx-link>
    <em class="ml5 mr5">-</em>
    <span>{{ item.value }}</span>
  </template>
</fx-autocomplete>

export default defineComponent({
    setup() {
        const querySearch = (searchText: string, cb: (val: { value: string, role: string }[]) => void) => {
          const restaurants = [
            { value: '蒙奇·D·路飞', role: '船长' },
            { value: '罗罗诺亚·索隆', role: '剑士' },
            { value: '文斯莫克·山治', role: '厨师' },
            { value: '托尼托尼·乔巴', role: '医生' },
            { value: '娜美', role: '航海士' },
            { value: '乌索普', role: '狙击手' },
            { value: '妮可·罗宾', role: '考古学家' },
            { value: '弗兰奇', role: '船匠' },
            { value: '布鲁克', role: '音乐家' },
            { value: '甚平', role: '舵手' }
          ]
          const results = searchText ? restaurants.filter((item: { value: string, role: string }) => {
            return item.role.match(searchText)
          }) : restaurants
          cb(results)
      }
    }
})`
    const icon = `<div class="type mb10">
  <span>属性方式:</span>
  <fx-input class="mr10" v-model="suffixInput" placeholder="请输入内容" suffix-icon="icon-search"></fx-input>
  <fx-input class="mr10" v-model="prefixInput" placeholder="请输入内容" prefix-icon="icon-search"></fx-input>
</div>
<div class="slot">
  <span>插槽方式:</span>
  <fx-input class="mr10" v-model="suffixInput" placeholder="请输入内容">
    <template #suffix>
      <i class="iconfont icon-search"></i>
    </template>
  </fx-input>
  <fx-input class="mr10" v-model="prefixInput" placeholder="请输入内容">
    <template #prefix>
      <i class="iconfont icon-search"></i>
    </template>
  </fx-input>
</div>`
    const size = `<fx-input class="mr10" v-model="sizeInput" size="large" placeholder="请输入内容"></fx-input>
<fx-input class="mr10" v-model="sizeInput" size="middle" placeholder="请输入内容"></fx-input>
<fx-input class="mr10" v-model="sizeInput" size="default" placeholder="请输入内容"></fx-input>
<fx-input class="mr10" v-model="sizeInput" size="mini" placeholder="请输入内容"></fx-input>`
    const textarea = '<fx-input class="mr10" v-model="textareaInput" type="textarea" placeholder="请输入内容"></fx-input>'

    const querySearch = (searchText: string, cb: (val: { value: string, role: string }[]) => void) => {
      const results = searchText ? defaultList.filter((item: { value: string, role: string }) => {
        return item.value.match(searchText)
      }) : defaultList
      cb(results)
    }

    const querySearch2 = (searchText: string, cb: (val: { value: string, role: string }[]) => void) => {
      const results = searchText ? defaultList.filter((item: { value: string, role: string }) => {
        return item.role.match(searchText)
      }) : defaultList
      cb(results)
    }

    return {
      base,
      disabled,
      clear,
      round,
      password,
      search,
      search2,
      icon,
      size,
      textarea,
      baseInput,
      sizeInput,
      roundInput,
      clearInput,
      disabledInput,
      passwordInput,
      searchInput,
      searchInput2,
      textareaInput,
      suffixInput,
      prefixInput,
      querySearch,
      querySearch2
    }
  }
})
